// eslint-disable-next-line camelcase
export const studentsVoucher = `
  select
    s.name as studentName
    , s.schoolName as schoolName
    , s.city as cityName
    , s.state as stateName
    , s.createdAt

  from students as s

  where s.createdAt between ? and ?
  and s.voucherIssued is not null
  order by 1
`;
